"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"

export function Navigation() {
  const [scrolled, setScrolled] = useState(false)
  const pathname = usePathname()

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const isActive = (path: string) => pathname === path

  return (
    <nav className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${scrolled ? "glass" : ""}`}>
      <div className="max-w-6xl mx-auto px-6 lg:px-8 py-6">
        <div className="flex items-center justify-between">
          <Link href="/" className="text-sm font-medium hover:opacity-60 transition-opacity">
            Portfolio
          </Link>

          <div className="flex items-center gap-8">
            <Link
              href="/work"
              className={`text-sm transition-colors ${
                isActive("/work") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              Work
            </Link>
            <Link
              href="/photos"
              className={`text-sm transition-colors ${
                isActive("/photos") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              Photos
            </Link>
            <Link
              href="/travel"
              className={`text-sm transition-colors ${
                isActive("/travel") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              Travel
            </Link>
            <Link
              href="/contact"
              className={`text-sm transition-colors ${
                isActive("/contact") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              Contact
            </Link>
          </div>
        </div>
      </div>
    </nav>
  )
}
